import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import { ContactProps } from '../../../types';
import PageNavigation from '../PageNavigation/PageNavigation';

import border_dark from '../../../img/border_dark.svg';

import './Contact.css';
import '../shared-styles.css';

const Contact: React.FC<ContactProps> = ({ setDialogText, setCurrentAction }) => {
  const [name, setName] = useState<string>('');
  const [email, setEmail] = useState<string>('');
  const [message, setMessage] = useState<string>('');
  const [sending, setSending] = useState<boolean>(false);
  const [sent, setSent] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);
  const [errorMsg, setErrorMsg] = useState<string>('');

  const form = useRef<HTMLFormElement>(null);

  const sendEmail = (e: React.FormEvent) => {
    e.preventDefault();
    setSending(true);
    if (form.current) {
      emailjs
        .sendForm('service_q3stgbj', 'template_hb21y1x', form.current, 'ez1gnij5WiOdzyc5t')
        .then(
          (result) => {
            console.log(result.text);
            setSending(false);
            setSent(true);
          },
          (error) => {
            console.error(error.text);
            setError(true);
            setErrorMsg(error.text);
          }
        );
    }
  };

  const renderForm = () => {
    if (sending) {
      return (
        <div id="sending-mail">
          <i className="spinner icon"></i>
        </div>
      );
    }
    if (sent && !error && !sending) {
      return (
        <div className="ui positive message">
          <div className="header">Message sent succesfully.</div>
        </div>
      );
    } else {
      return (
        <>
          <p>Feel free to leave a message. I'll get back to you as soon as possible!</p>
          {!error || (
            <div className="ui negative message">
              <div className="header">{errorMsg}</div>
            </div>
          )}
          <form className="ui form" id="contact-form" ref={form} onSubmit={sendEmail}>
            <div className="field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>Name:</label>
              <input
                id="name"
                name="name"
                type="text"
                placeholder="Your name"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>Email:</label>
              <input
                id="email"
                name="email"
                type="email"
                placeholder="your@email.com"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>Message:</label>
              <textarea
                id="message"
                name="message"
                placeholder="Type your message here..."
                value={message}
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              ></textarea>
            </div>
            <button className="retro-button primary" type="submit">
              ✉️ Sign Guestbook
            </button>
          </form>
        </>
      );
    }
  };
  const guestbookCount = Math.floor(Math.random() * 9999) + 1000;

  return (
    <div id="contact" className="retro-page">
      <PageNavigation currentSection="goTo-contact" onNavigate={setCurrentAction} />

      <div className="page-content">
        <h1>✏️ Sign My Guestbook! ✏️</h1>

        <div className="retro-box">{renderForm()}</div>

        <div
          className="visitor-counter"
          style={{ margin: '20px auto', textAlign: 'center', display: 'table' }}
        >
          {guestbookCount} visitors signed!
        </div>
      </div>
    </div>
  );
};

export default Contact;
